import React from "react";
import s from './Main.module.scss'
import sContainer from '../../common/styles/Container.module.scss'
import myPhotoImage from "../../assets/image/main/photo_main.jpg";


function Main() {

    const myPhoto = {
        backgroundImage: `url(${myPhotoImage})`
    }

    return (

        <div className={s.mainBlock} id="main">
            <div className={`${sContainer.container} ${s.container}`}>
                <div className={s.introduce}>
                    <span>!</span>
                    <h1>  <span> </span></h1>
                    <p><b>Frontend   -.</b></p>
                    <p className={s.info}><span className={s.mainInfo}><b>   .</b></span> <br/> 
                        <b> SPA</b>-,  <b>React, Redux, TypeScript</b>,   <b>Angular</b>,   
                            . <br/><b></b>     .<br/><b>    </b> -, 
                           . <br/> <b>    </b>. <br/> 
                          -    (React Native).</p>
                </div>
                <div className={s.photo}>
                    <div className={s.image} style={myPhoto}></div>
                </div>
            </div>
        </div>

    );
}

export default Main;